html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.container {
  background-image: url("https://images.unsplash.com/photo-1514315153150-cd7d8d716178?ixlib=rb-1.2.1&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=1080&fit=max&ixid=eyJhcHBfaWQiOjF9");
  width: 50%;
  border: 3px solid #72A0C1;
  box-shadow: 2px 2px #2F424F;
  text-align: center;
  margin: 20px auto;
  padding-bottom: 0px;
}

.form {
  margin: 5px;
  padding: 5px;
}

.input-box {
  margin: 10px;
  text-align: center;
  border-radius: 5px;
  padding: 15px 22px;
}

.weather {
  border: 3px solid #676767;
  background-image: url("https://images.unsplash.com/photo-1548191269-4a4f5f8856f5?ixlib=rb-1.2.1&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=1080&fit=max&ixid=eyJhcHBfaWQiOjF9");
}

.button {
   background-color: #5D8AA8;
  border: none;
  color: white;
  padding: 15px 22px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 10px;
}

.button:hover {
  background-color: #4CAF50; /* Green */
  color: white;
  border-radius: 10px;
}